import React from 'react'
import Typewriter from "typewriter-effect";

const Typing = (props) => {
    const {sentence,color,values, senSize, valSize, valStyle} = props;
    
    return (
        <div className="d-flex container px-0">
            <span className={`${senSize} text-light me-2`}>{sentence}</span>
            <span className={`${valSize} ${valStyle}`} style={{color}}>
                <Typewriter
                    options={{
                        strings: values,
                        autoStart: true,
                        loop: true,
                      }}
                />
            </span>
        </div>
    )
}

export default Typing
