import { BrowserRouter, Routes, Route } from "react-router-dom";
import { useContext, useEffect, useState } from "react";
import Header from "./components/Header";
import LoadingBar from "react-top-loading-bar";
import AlertMsg from "./components/master/AlertMsg";
import NoInternet from "./components/Error/NoInternet";
import Maintanence from "./components/Error/Maintanence";
import Dashboard from "./components/Dashboard";
import ProtfolioDetails from "./components/Portfolio/PortfolioDetails";
import ScrollToTop from "./components/master/ScrollToTop";
import Footer from "./components/Footer";
import NoPage from "./components/Error/NoPage";
import ServerDown from "./components/Error/ServerDown";
import MyContext from "./context/MyContext";
import './App.css';
import Aos from "aos";


const App = () => {

  const { serverIsOnline, maintanenceStatus, progress} = useContext(MyContext)
  const [isOnline, setIsOnline] = useState(navigator.onLine);
  const [content, setContent] = useState(null);

  useEffect(() => {
    Aos.init();
    setInterval(() => {
      document.title = 'DR | Full Stack Developer';
    }, 5000);

    setInterval(() => {
      document.title = 'Deepak Raikwar (@rai_deepak_07)';
    }, 10000);

    const handleOnline = () => {
      setIsOnline(true)
    }
    const handleOffline = () => {
      setIsOnline(false);
    }

    if(isOnline===false){
      setContent(<NoInternet/>)
    }else if(serverIsOnline===false){
      setContent(<ServerDown/>)
    }else if(maintanenceStatus===true){
      setContent(<Maintanence/>)
    }else{
      setContent(null)
    }
  

    window.addEventListener('online', handleOnline);
    window.addEventListener('offline', handleOffline);

    return () => {
      window.removeEventListener('online', handleOnline);
      window.removeEventListener('offline', handleOffline);
    };
        
  }, [isOnline, serverIsOnline, maintanenceStatus]);

  return (
    <>
      <BrowserRouter basename="raideepak07">
        <Header />
        <LoadingBar color='#f11946' height="3px" loaderSpeed= "1000" shadow={true} progress={progress} />
        <AlertMsg />
        <Routes>
          <Route path="/" element={content!=null?content:<Dashboard/>} />
          <Route path="/project_details/:id" element={content!=null?content:<ProtfolioDetails/>} />
          <Route path="*" element={<NoPage />} />
        </Routes>
        <ScrollToTop/>
        <Footer />
      </BrowserRouter >
    </>
  )
}

export default App