import React from 'react'

const MasterPage = (props) => {
    const {title, subtitle, row, containerDesign} = props;
    return (
        <div className='SectionMng'>
            <div className={`container ${containerDesign}`} style={{ height: "100%" }}>

                <div className="row">
                    <div className="col-md-12">
                        <div className='MyHead1'>{title}</div>
                        <div className='MySubHead1 text-muted'>{subtitle}</div>
                    </div>
                </div>

                <div>
                    {row}
                </div>
            </div>
        </div>
    )
}

export default MasterPage
