import React, { useContext } from 'react'
import myContext from '../context/MyContext';
import Typing from './master/Typing';
import Skeleton from 'react-loading-skeleton';

const Home = () => {

  const { homeData } = useContext(myContext);
  
  // Style for Transparent Background
  const HomeStyle = {
    background: "rgba(0,0,0,0.3)",
    zIndex: "-1"
  }

  // Typing Effect Values
  let sentence = "|"
  let typingvalues = ['Full Stack Developer', 'Web Developer', 'Python Developer']


  return (
     <div className="container-fluid" style={HomeStyle}>
      <div className='container txt3' >
        <div className="row" style={{ minHeight: "100vh" }}>

          {/* Text Name and Prof Start */}
          <div className="col-md-12 d-flex justify-content-center flex-column px-lg-5 text-light px-4  placeholder-glow">
            <span className='display-5'>Hello, </span>
              <span className='display-2 lh-base'>I'm<span className="display-6 txt1 text-danger"> {homeData!==undefined?homeData.Name:<span className='placeholder fs-3 bg-body-secondary col-md-2 col-4'></span>}</span></span>
            <Typing sentence={sentence} color="orange" senSize="display-6 lh-lg" valSize="display-6 lh-lg" values={typingvalues} />

            {/* CV Download Button Start */}
            <div className='mt-5' data-aos="fade-right" data-aos-duration="1500">
              <a href={homeData!==undefined?homeData.Cv_URL:<Skeleton/>} target='_blank' rel='noreferrer' className="btn btn-outline-warning rounded-0 btnIconHover">
                Download CV<i className="fa fa-download hvr-icon px-2"></i>
              </a>
            </div>
            {/* CV Download Button End */}
          </div>
          {/* Text Name and Prof. End */}
        </div>
      </div>
    </div>

  )
}

export default Home
