import React, { useContext, useEffect, useState } from 'react'
import myBg from '../../assets/img/b5.jpg'
import bgImg from '../../assets/img/main.png'
import MyContext from '../../context/MyContext'

const Maintanence = () => {

    const { maintanenceData } = useContext(MyContext)

    let MainBgStyle = {
        background: `url(${myBg})`,
        backgroundSize: "cover",
        backgroundRepeat: "no-repeat",
    }

    const TimeBg = 'border rounded-3 shadow-lg py-sm-3 py-2 px-sm-5 px-4 mx-1'
    const DivBg = {
        background: "rgba(0,0,0,0.6)"
    }
    const timerStyle = "display-5 txt3 fw-semibold text-light"
    const textStyle = "h6 text-uppercase txt text-warning border-2 border-top border-warning pt-2"

    const [day, setDay] = useState(0);
    const [hrs, setHrs] = useState(0);
    const [min, setMin] = useState(0);
    const [sec, setSec] = useState(0);

    const getTime = (EndDate) => {
        const time = Date.parse(EndDate) - Date.now();

        let c_day = Math.floor(time / (1000 * 60 * 60 * 24))
        setDay(c_day);
        let c_hrs = Math.floor((time / (1000 * 60 * 60)) % 24)
        setHrs(c_hrs);
        let C_min = Math.floor((time / 1000 / 60) % 60)
        setMin(C_min);
        let c_sec = Math.floor((time / 1000) % 60)
        setSec(c_sec);
    }

    useEffect(() => {
        if(maintanenceData!=null){
            const interval = setInterval(() => getTime(maintanenceData.EndDate));
            return () => clearInterval(interval);
        }
        // eslint-disable-next-line
    }, [maintanenceData != null ? maintanenceData.EndDate : '']);

    return (
        <div className='container-fluid px-0' style={MainBgStyle}>
            <div className="container text-light">
                <div className="row" style={{ height: "100vh" }}>

                    <div className="col-lg-6 d-flex flex-column justify-content-center text-center py-lg-0 py-md-5 mt-lg-1 mt-5">
                        <div className="row">
                            <div className="col-lg-0 col-md-2"></div>
                            <div className="col-lg-12 col-md-8">
                                <a href="https://rai-deepak-07.github.io/raideepak07/">
                                    <img src={bgImg} alt="" className='img-fluid w-50 shadow-0' />
                                </a>
                            </div>
                            <div className="col-lg-0 col-md-2"></div>
                        </div>
                        <div>
                            <span className='display-4 border-bottom border-2 text-capitalize txt py-3'>{maintanenceData? maintanenceData.Title : ''}</span>
                            <p className='txt3 fs-6 mt-5 px-sm-5 px-2 text-capitalize'>{maintanenceData ? maintanenceData.Message : ''}</p>
                        </div>
                    </div>

                    <div className="col-lg-6 text-lg-start text-center d-flex flex-column justify-content-center pb-lg-0 pb-md-5">
                        <div className="row mb-3">
                            <div className="col txt2">
                                <h4>The Maintenance will ends on</h4>
                            </div>
                        </div>

                        <div className="row">
                            <div className="col d-flex justify-content-lg-between justify-content-evenly text-center">
                                <div className={TimeBg} style={DivBg}>
                                    <span className={`${timerStyle}`}>{day}</span>
                                    <div className={`${textStyle}`}>days</div>
                                </div>
                                <div className={TimeBg} style={DivBg}>
                                    <span className={`${timerStyle}`}>{hrs}</span>
                                    <div className={`${textStyle}`}>hrs</div>
                                </div>
                                <div className={TimeBg} style={DivBg}>
                                    <span className={`${timerStyle}`}>{min}</span>
                                    <div className={`${textStyle}`}>mins</div>
                                </div>
                                <div className={TimeBg} style={DivBg}>
                                    <span className={`${timerStyle}`}>{sec}</span>
                                    <div className={`${textStyle}`}>secs</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Maintanence
