import React from 'react'

const SkillSets = ({ title, end}) => {
    return (
        <div className='mb-4' data-aos="zoom-in-up" data-aos-duration="1000" data-aos-delay="1000" >
            <h6 className='fw-semibold text-capitalize txt mb-0'>{title}</h6>
            <div className="row">
                <div className="col-lg-11 col-10">
                    <div className="progress rounded-0 mt-2" role="progressbar" aria-label="Warning example" aria-valuenow="75" aria-valuemin="0" aria-valuemax="100" style={{height:"10px"}}>
                        <div className='progress-bar color' style={{ width: `${end}%`  }}></div>
                    </div>
                </div>
                <div className="col-lg-1 col-2">
                    {end}%
                </div>
            </div>
        </div>
    )
}

export default SkillSets
