import React, { useContext, useEffect, useState } from 'react'
import { Link, useParams } from 'react-router-dom'
import myContext from '../../context/MyContext'

const PortfolioDetails = () => {

    let ul_i = ""
    const { id } = useParams();
    const [prjData, setPrjData] = useState(null);
    const [Images, setImages] = useState(null);
    const { fetchProjectDetails, fetchProjectImageDetails } = useContext(myContext);


    const fetchData = async () => {
        const project_details = await fetchProjectDetails(id)
        if (project_details !== undefined) {
            setPrjData(project_details[0])
        }
        const project_images = await fetchProjectImageDetails(id)
        // console.log(project_images);
        setImages(project_images)
    }

    useEffect(() => {
        fetchData()
        // eslint-disable-next-line
    }, []);

    return (prjData &&
        <div id="PortfolioDetails" className="container-fluid">
            <div className="row bg-light py-4">
                <div className="col-12">
                    <div className="container px-lg-5">
                        <div className="row d-flex">
                            <div className="col-12 d-flex justify-content-between flex-md-row flex-column">
                                <h4 className='m-0 txt2'>Portfolio Details</h4>

                                <nav aria-label="breadcrumb" className='align-self-md-center mt-md-0 mt-2'>
                                    <ol className="breadcrumb m-0 txt2">
                                        <li className="breadcrumb-item"><Link className='link' to="/">Home</Link></li>
                                        <li className="breadcrumb-item active" aria-current="page">Portfolio</li>
                                    </ol>
                                </nav>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div className="container px-lg-5 my-5">
                <div className="row">
                    <div className="col-lg-7 px-0">
                        {/* 
                        <div id="carouselExampleRide" className="carousel slide" data-bs-ride="carousel">
                            <div className="carousel-inner">
                                {
                                    Images != null ? Images.map((item, index) => {
                                        return (
                                            <div key={index} className={`carousel-item ${index === 0 ? 'active' : ''}`} data-mdb-interval="500">
                                                <img src={item.Prj_ImageURL} className="img-fluid" alt="..." />
                                                </div>
                                                )
                                            }) : ""
                                        }
                                        </div>
                                    </div> */}
                        <div class="scroll-container">
                            {
                                Images != null ? Images.map((item, index) => {
                                    return (
                                        <img key={index} src={item.Prj_ImageURL} className="img-fluid" alt="..." />
                                    )
                                }) : ""
                            }
                        </div>

                    </div>
                    <div className="col-lg-1"></div>

                    <div className="col-lg-4 mt-lg-0 mt-5">
                        <div className="row shadow py-4 px-md-4 px-3">
                            <div className="col">
                                <h4 className='txt2 fw-semibold'>Project Information</h4>
                                <hr className='border' />
                                <label><i className={ul_i}></i>Category: <span className='text-muted'>{prjData.Prj_Tech.Tech_Type.Category_Type}</span></label>
                                <br />
                                <label><i className={ul_i}></i>Technology: <span className='text-muted'>{prjData.Prj_Tech.Tech_Name}</span></label>
                                <br />
                                <label><i className={ul_i}></i>Client: <span className='text-muted'>MySelf</span></label>
                                <br />
                                <label><i className={ul_i}></i>Project Name: <span className='text-muted'>{prjData.Prj_Name}</span></label>
                                <br />
                                <label><i className={ul_i}></i>Project URL: <a href={prjData.Prj_URL} target='_blank' rel='noreferrer'>{prjData.Prj_URL}</a></label>
                            </div>
                        </div>
                        <div className="row mt-5">
                            <div className="col">
                                <h4 className='txt pb-2 fw-semibold border-bottom'>Details About {prjData.Prj_Name}</h4>
                                <p className='txt3'>
                                    {prjData.Prj_Details}
                                </p>
                            </div>
                        </div>

                    </div>
                </div>
            </div>
        </div>
    )
}

export default PortfolioDetails
