import React, { useEffect, useState } from 'react'
import Count from 'react-countup';

const CoutCard = (props) => {

    const { id, icon, countTitle, countEnd } = props;
    const [isVisible, setIsVisible] = useState(false);

    const checkVisibility = () => {
        let divElement = document.getElementById(`CheckCount${id}`);
        let bounding = divElement.getBoundingClientRect(); // Get the position and dimensions of the div
        // const newIsVisible = bounding.top >= 0 && bounding.bottom <= window.innerHeight; // Check if div is fully visible
        // const newIsVisible = bounding.top < window.innerHeight && bounding.bottom >= 0;
        const threshold = 0.5; // Define the desired visibility threshold (50% in this case)
        let elementHeight = bounding.bottom - bounding.top;
        let visibleHeight = Math.min(window.innerHeight, bounding.bottom) - Math.max(0, bounding.top);
        let newIsVisible = visibleHeight >= threshold * elementHeight;

        setIsVisible(newIsVisible);
    };

    useEffect(() => {
        checkVisibility(); // Call the function initially

        // Attach event listeners to scroll, resize, and orientation change events
        window.addEventListener('scroll', checkVisibility);
        window.addEventListener('resize', checkVisibility);
        window.addEventListener('orientationchange', checkVisibility);

        // Clean up the event listeners when the component unmounts
        return () => {
            window.removeEventListener('scroll', checkVisibility);
            window.removeEventListener('resize', checkVisibility);
            window.removeEventListener('orientationchange', checkVisibility);
        };
        // eslint-disable-next-line
    }, [id]);


    return (
        <div id={`CheckCount${id}`} className="CountCard col-md-6 txt3 mb-lg-0 my-4">
            <div className='text-center border pt-5 pb-4 position-relative' style={{ background: "white" }}>
                <div className='position-absolute countCardIcon top-0 start-50 translate-middle rounded-circle'>
                    <i className={`${icon}`}></i>
                </div>
                <span className='title fw-semibold txt3 display-6'>
                    {countEnd==='Fresher'?
                        <span className='fs-2'>
                            {countEnd}
                        </span>
                        :
                        <Count start={0} end={isVisible ? countEnd : 0} duration={3} />

                    }
                </span>
                <p className='fs-5 txt2 mt-1'>{countTitle}</p>
            </div>
        </div>
    )
}

export default CoutCard
