import React from 'react'
import ResumeCard from './ResumeCard'
import { useContext } from 'react'
import myContext from '../../context/MyContext'
import Skeleton from 'react-loading-skeleton'

const Resume = () => {

  const { resumeData } = useContext(myContext)

  return (

    <div className="container-fluid px-0">
      {resumeData?
        <div id='resume' className="row">
          {
            resumeData.map((item, index) => {
              return (
                <ResumeCard key={index} ResumeDetails={item} />
              )
            })
          }
        </div>
        :
              // Placehoder Loading for when resumeData not get the the data 
              <div id='resume' className="row">
                <div className="col-lg-6 mt-5">
                <h3><Skeleton/></h3>
                <div className="resume_item px-4">
                    <h6 className='pb-3 fs-2'><Skeleton/></h6>
                    <div className='placeholder-glow'>
                        <span className='placeholder col-4 p-3 me-3'></span>
                        <br className='d-sm-none' />
                        <br className='d-sm-none' />
                        <span className='placeholder col-6 fs-4'></span>
                    </div>
                    <p className='pt-3 fst-italic fw-bold txt5 fs-5'><Skeleton/></p>
                    <p className='fs-5'><Skeleton count={4}/></p>
                </div>
                </div>
              </div>
      }
    </div>
  )
}

export default Resume
