import React, { useEffect, useState } from 'react'

const Clock = () => {
    const [time, setTime] = useState('');
    const [date, setDate] = useState('');
    
    useEffect(() => {
        
        const options = { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' };
        const interval = setInterval(() => {
          const a = new Date();
          const currentTime = a.getHours() + ':' + a.getMinutes() + ':' + a.getSeconds();
          const currentDate = a.toLocaleDateString(undefined, options);
    
          setTime(currentTime);
          setDate(currentDate);
        }, 1000);
    
        // Clear the interval when the component unmounts
        return () => clearInterval(interval);
      }, [time, date]);

  return (
    <div className='text-light txt2 d-flex justify-content-md-between flex-md-row flex-column-reverse align-items-end'>
      <span className='fs-5 mt-md-0 mt-2'><i className='bi bi-clock text-warning pe-2'></i>{time}</span>
      <span className='fs-5'>{date}</span>
    </div>
  )
}

export default Clock
