import React from 'react'

const ResumeItem = ({course, year, city, institute, details}) => {
    return (

        // Resume Item Start 
        <div className="resume_item txt3 px-4 pb-2">
            <h6 className='text-uppercase fs-5 pb-3 txt fw-semibold'>{course}</h6>
            <div className='fw-bold'>
                <span className='item_year me-4'>{year}</span>
                <br className='d-sm-none' />
                <br className='d-sm-none' />
                <span className='item_city px-sm-4 fs-5 fst-italic txt5'>{city}</span>
            </div>
            <p className='pt-3 fst-italic fw-bold txt5 fs-5'>{institute}</p>
            <p className='details fs-6 txt3 lh-lg'>{details}</p>
        </div>
    )
}

export default ResumeItem
