import React from 'react'
import SkillSets from './SkillSets';
import { useContext } from 'react';
import myContext from '../../context/MyContext';
import Skeleton from 'react-loading-skeleton';
const Skills = () => {

  const { skillData, skillItemData } = useContext(myContext);

  return (

    <div id='skill' className="container-fluid mt-5 px-0">
      <div className="row">
        {skillData ?
          skillData.map((item, index) => {
            return (
                <div key={index} className="col-md-6 mb-5 pe-lg-5 pe-md-5 txt2 fw-semibold" data-aos={`${index%2===0?'fade-up':'fade-up'}`} data-aos-duration="2000">
                  <div className='text-capitalize fs-5'>{item.Title}</div>
                  <hr className='mb-4' />
                  {skillItemData ?
                    skillItemData.filter(skillitem => skillitem.Title.Skill_Id === item.Skill_Id).map((item, index) => {
                      return (
                        <SkillSets key={index} title={item.Name} end={item.Progress} />
                      )
                    }
                    ) : <Skeleton count={3} />
                  }
                </div>
            )
          })
          :
          <>
            <div className="col-lg-6 pe-lg-5 placeholder-glow">
              <h2><Skeleton /></h2>
              <hr />
              <span className='placeholder col-4 mb-3 py-3'></span>
              <span><Skeleton count={5} /></span>
            </div>
            <div className="col-lg-6 pe-lg-5 placeholder-glow">
              <h2><Skeleton /></h2>
              <hr />
              <span className='placeholder col-4 mb-3 py-3'></span>
              <span><Skeleton count={5} /></span>
            </div>

          </>
        }
      </div>
    </div>
  )
}

export default Skills
