import React, {useContext} from 'react'
import ServicesItems from './ServicesItems'
import myContext from '../../context/MyContext';

const Services = () => {
  
  const {servicesData} = useContext(myContext);

  return (
    <>
      {servicesData ? (
        <div className='row mt-3'>
          {servicesData.map((item,index) => (
            <ServicesItems key={index} icon={item.Service_Icon} title={item.Service_Name} details={item.Service_Details} />
          ))}
        </div>
      ) : (
        <div className="col-lg-4 col-md-6 col-12 py-5">
          <div className="card text-center bg-transparent shadow-none px-5 py-4" aria-hidden="true">
            <img src="..." className="card-img-top" alt="..." />
            <div className="card-body">
              <h5 className="card-title placeholder-glow">
                <span className="placeholder col-8"></span>
              </h5>
              <p className="card-text placeholder-glow">
                <span className="placeholder col-10"></span>
                <span className="placeholder col-12"></span>
                <span className="placeholder col-10"></span>
              </p>
            </div>
          </div>
        </div>
      )}
    </>
  )
}

export default Services
