import React, { useContext, useEffect } from 'react'
import PortfolioProject from './PortfolioProject'
import myContext from '../../context/MyContext';
import { useState } from 'react';

const Portfilo = () => {
  const { projectData, categoryData, fetchProjectData } = useContext(myContext)
  const [ activeBtn, setactiveBtn ] = useState(0) 
  const [ filterdData, setFilterData ] = useState(null) 
  console.log();

  const filterProject = (id) =>{
    setactiveBtn(id)
    const filteredProjects = projectData.filter(project => project.Prj_Tech.Tech_Type.Category_Id === id);
    setFilterData(filteredProjects)
  }

  const fetchData = async ()=>{
    setactiveBtn(0);
    await fetchProjectData()
  }

    useEffect(() => {
      fetchData()
      // eslint-disable-next-line
  }, []);


  return (
    <>
      <div className="row mt-5" id='Portfolio'>
        <div className="col text-center">
          <button type="button" className={`btn ${activeBtn===0?'active':''}`} onClick={()=>{fetchData()}}>All</button>
          {categoryData ? categoryData.map((item, index) => {
            return (
              <button key={index} type="button" className={`btn ${activeBtn===item.Category_Id?'active':''}`} onClick={()=>filterProject(item.Category_Id)}>{item.Category_Type.split(' ').reverse()[0]}</button>

            )
          }) :
            <span>
              <button type="button" className='btn btn-warning disabled placeholder col-1' aria-disabled="true"></button>
              <button type="button" className='btn btn-warning disabled placeholder col-1' aria-disabled="true"></button>
              <button type="button" className='btn btn-warning disabled placeholder col-1' aria-disabled="true"></button>
            </span>
          }
        </div>
      </div>

      {(activeBtn===0? projectData:filterdData)? (
        <div className='row mt-5'>
            {(activeBtn===0?projectData:filterdData)
              .map((item, index) => (
                <PortfolioProject key={index} PrjId={item.Project_Id} PrjImageUrl={item.Prj_ImageURL} PrjUrl={item.Prj_URL} PrjName={item.Prj_Name} PrjTech={item.Prj_Tech} />
              )
            )}
        </div>
      ) : (
        <div className='mt-5 col-lg-4 d-flex flex-column placeholder-glow'>
          <span className='placeholder col-12' style={{fontSize:"200px"}}></span>
          {/* <span className="placeholder col-12 placeholder-lg"></span>
          <span className="placeholder col-8 my-2"></span>
          <span className="placeholder col-12 placeholder-sm"></span>
          <span className="placeholder col-8 my-2"></span>
          <span className="placeholder col-12 placeholder-lg"></span> */}
        </div>
      )}

    </>
  )
}

export default Portfilo
