import React from 'react'
import { Link } from 'react-router-dom'

const PortfolioProject = ({ PrjId, PrjImageUrl, PrjUrl, PrjName, PrjTech }) => {

    return (
        <div className='col-lg-4 col-md-6 mb-4' data-aos="zoom-in" data-aos-duration="1500">
            <div className="portfolioCard border">
                <img src={PrjImageUrl} className="img-fluid" alt="" />
                <div className="portfolio-info text-light txt3">
                    <h2>{PrjName}</h2>
                    <p className='fs-5'>{PrjTech.Tech_Name}</p>

                    <div>
                        <Link to={`project_details/${PrjId}`}>
                            <i className="bi bi-plus-lg"></i>
                        </Link>
                        <a href={PrjUrl} target='_blank' rel='noreferrer'>
                            <i className="bi bi-link-45deg"></i>
                        </a>
                    </div>
                </div>
            </div>

        </div>
    )
}

export default PortfolioProject
