import { useEffect, useState } from 'react'
import MyContext from './MyContext'
import apiService from '../apiservice/apiService';
// import ServerDown from '../components/Error/ServerDown';

const MyState = (props) => {

    // All Enviornment Vairable URL
    const urls = {
        "HomePage": { "url": process.env.REACT_APP_SITE_HOMEPAGE_URL, "title": "HomePage" },
        "LinkedIn": { "url": process.env.REACT_APP_LINKEDIN_URL, "title": "Linkedin" },
        "GitHub": { "url": process.env.REACT_APP_GITHUB_URL, "title": "GitHub" },
        "Facebook": { "url": process.env.REACT_APP_FACEBOOK_URL, "title": "Facebook" },
        "Instagram": { "url": process.env.REACT_APP_INSTA_URL, "title": "Instagram" },
        "Twitter": { "url": process.env.REACT_APP_TWITTER_URL, "title": "Twitter" },
        "Email": { "url": process.env.REACT_APP_EMAIL, "title": process.env.REACT_APP_EMAIL?.replace('mailto:','')},
        "Mobile": { "url": process.env.REACT_APP_MOBILE_NO, "title": process.env.REACT_APP_MOBILE_NO?.replace('tel:','') },
        "Location": { "url": process.env.REACT_APP_ADDRESS, "title": "Delhi" },
    }

    // AlertMsg Component State
    const [alert, setAlert] = useState({
        type: "info",
        msg: "",
        time: 0,
        open: false,
    });

    // AlertMsg Component State Manage Via Function
    const SetAlert = (type, msg, time, status) => {
        const alert = setInterval(() => {
            setAlert({
                type: type, msg: msg, time: time, open: status,
            })
            myStopFunction()
        }, 500);

        const myStopFunction = () => {
            clearInterval(alert);
        }
    }

    // Top Loading State and State Manage via Fuction 
    const [progress, setProgress] = useState(0);
    const LoadingProgress = (value) => {
        setProgress(value)
    }

    const [serverIsOnline, setserverIsOnline] = useState(false);
    const [maintanenceStatus, setMaintanenceStatus] = useState(true)
    const [homeData, setHomeData] = useState({});
    const [servicesData, setServicesData] = useState(null);
    const [categoryData, setCategoryData] = useState(null);
    const [projectData, setProjectData] = useState(null);
    const [resumeData, setResumeData] = useState(null);
    const [resumeItemData, setResumeItemData] = useState(null);
    const [maintanenceData, setMaintanenceData] = useState(null);
    const [skillData, setSkillData] = useState(null);
    const [skillItemData, setSkillItemData] = useState(null);
    const [cardData, setCardData] = useState(null);

    const getTime = async (endDate) => {
        const time = Date.parse(endDate) - Date.now();

        let c_day = Math.floor(time / (1000 * 60 * 60 * 24))
        let c_hrs = Math.floor((time / (1000 * 60 * 60)) % 24)
        let C_min = Math.floor((time / 1000 / 60) % 60)
        let c_sec = Math.floor((time / 1000) % 60)

        if ((c_day <= 0) && (c_hrs <= 0) && (C_min <= 0) && (c_sec <= 0)) {
            setMaintanenceStatus(false)
        }
        else {
            setMaintanenceStatus(true)
        }
    };

    const fetchMaintanenceData = async () => {
        try {
            const response = await apiService.get('maintanence/')
            if (response.status === 200) {
                setserverIsOnline(true)
                setMaintanenceData(response.data[0])
                await getTime(response.data[0].EndDate)
            }
        } catch (error) {
            if (error) {
                if (error.response) {
                    if (error.response.status === 401) {
                        console.log(error.response.data.detail);
                    }
                    if (error.response.status === 404) {
                        console.log('Invalid API Url');
                    }
                } else {
                    setserverIsOnline(false)
                    console.log('Server Not Started');
                }
            }
        }
    };

    const fetchHomeData = async () => {
        try {
            const response = await apiService.get('about/')
            if (response.status === 200) {
                setHomeData(response.data[0])
            }
        } catch (error) {
            if (error) {
                if (error.response) {
                    if (error.response.status === 401) {
                        console.log(error.response.data.detail);
                    }
                    if (error.response.status === 404) {
                        console.log('Invalid API Url');
                    }
                } else {
                    console.log('Server Not Started');
                }
            }
        }
    };

    const fetchServicesData = async () => {
        try {
            const response = await apiService.get('services/')
            if (response.status === 200) {
                setServicesData(response.data)
            }
        } catch (error) {
            setServicesData(null)
            if (error) {
                if (error.response) {
                    if (error.response.status === 401) {
                        console.log(error.response.data.detail);
                    }
                    if (error.response.status === 404) {
                        console.log('Invalid API Url');
                    }
                } else {
                    console.log('Server Not Started');
                }
            }
        }
    };

    const fetchCategoryData = async () => {
        try {
            const response = await apiService.get('category/')
            if (response.status === 200) {
                setCategoryData(response.data)
            }
        } catch (error) {
            setCategoryData(null)
            if (error) {
                if (error.response) {
                    if (error.response.status === 401) {
                        console.log(error.response.data.detail);
                    }
                    if (error.response.status === 404) {
                        console.log('Invalid API Url');
                    }
                } else {
                    console.log('Server Not Started');
                }
            }
        }
    };

    const fetchProjectData = async () => {
        try {
            const response = await apiService.get('project/')
            if (response.status === 200) {
                setProjectData(response.data)
            }

        } catch (error) {
            setProjectData(null)
            if (error) {
                if (error.response) {
                    if (error.response.status === 401) {
                        console.log(error.response.data.detail);
                    }
                    if (error.response.status === 404) {
                        console.log('Invalid API Url');
                    }
                } else {
                    console.log('Server Not Started');
                }
            }
        }
    };

    const fetchProjectDetails = async (id) => {
        try {
            setProgress(20)
            const response = await apiService.get('project/?Project_Id=' + id)
            setProgress(50)
            if (response.status === 200) {
                setProgress(100)
                return (response.data)
            }
        } catch (error) {
            setProgress(100)
            if (error) {
                if (error.response) {
                    if (error.response.status === 401) {
                        console.log(error.response.data.detail);
                    }
                    if (error.response.status === 404) {
                        console.log('Invalid API Url');
                    }
                } else {
                    console.log('Server Not Started');
                }
            }
        }
    }

    const fetchProjectImageDetails = async (id) => {
        try {
            const response = await apiService.get('prjimage/?Prj_Img_Name=' + id)
            if (response.status === 200) {
                return (response.data)
            }

        } catch (error) {
            if (error) {
                if (error.response) {
                    if (error.response.status === 401) {
                        console.log(error.response.data.detail);
                    }
                    if (error.response.status === 404) {
                        console.log('Invalid API Url');
                    }
                } else {
                    console.log('Server Not Started');
                }
            }
        }
    };

    const fetchResumeData = async () => {
        try {
            const response = await apiService.get('resume/')
            if (response.status === 200) {
                setResumeData(response.data)
            }

        } catch (error) {
            setResumeData(null)
            if (error) {
                if (error.response) {
                    if (error.response.status === 401) {
                        console.log(error.response.data.detail);
                    }
                    if (error.response.status === 404) {
                        console.log('Invalid API Url');
                    }
                } else {
                    console.log('Server Not Started');
                }
            }
        }
    };

    const fetchResumeItemData = async () => {
        try {
            const response = await apiService.get('resumeitem/')
            if (response.status === 200) {
                setResumeItemData(response.data)
            }

        } catch (error) {
            setResumeItemData(null)
            if (error) {
                if (error.response) {
                    if (error.response.status === 401) {
                        console.log(error.response.data.detail);
                    }
                    if (error.response.status === 404) {
                        console.log('Invalid API Url');
                    }
                } else {
                    console.log('Server Not Started');
                }
            }
        }
    };

    const fetchSkillData = async () => {
        try {
            const response = await apiService.get('skill/')
            if (response.status === 200) {
                setSkillData(response.data)
            }

        } catch (error) {
            setSkillData(null)
            if (error) {
                if (error.response) {
                    if (error.response.status === 401) {
                        console.log(error.response.data.detail);
                    }
                    if (error.response.status === 404) {
                        console.log('Invalid API Url');
                    }
                } else {
                    console.log('Server Not Started');
                }
            }
        }
    };

    const fetchSkillItemData = async () => {
        try {
            const response = await apiService.get('skillitem/')
            if (response.status === 200) {
                setSkillItemData(response.data)
            }

        } catch (error) {
            setSkillItemData(null)
            if (error) {
                if (error.response) {
                    if (error.response.status === 401) {
                        console.log(error.response.data.detail);
                    }
                    if (error.response.status === 404) {
                        console.log('Invalid API Url');
                    }
                } else {
                    console.log('Server Not Started');
                }
            }
        }
    };

    const fetchCardData = async () => {
        try {
            const response = await apiService.get('card/')
            if (response.status === 200) {
                setCardData(response.data)
            }
            
        } catch (error) {
            setCardData(null)
            if (error) {
                if (error.response) {
                    if (error.response.status === 401) {
                        console.log(error.response.data.detail);
                    }
                    if (error.response.status === 404) {
                        console.log('Invalid API Url');
                    }
                } else {
                    console.log('Server Not Started');
                }
            }
        }
    };

    const fetchAllData = async () => {
        setProgress(10)
        await fetchHomeData()
        setProgress(20)
        await fetchCategoryData()
        setProgress(30)
        await fetchServicesData()
        setProgress(40)
        await fetchResumeData()
        setProgress(50)
        await fetchResumeItemData()
        setProgress(60)
        await fetchSkillData()
        setProgress(70)
        await fetchSkillItemData()
        setProgress(80)
        await fetchCardData()
        setProgress(100)
    }

    useEffect(() => {
        fetchMaintanenceData()
        if (maintanenceStatus === false) {
            fetchAllData()
        }
        const interval = setInterval(() => getTime(maintanenceData != null ? maintanenceData.EndDate : ''), 1000);
        return () => clearInterval(interval);
    }, [maintanenceData != null ? maintanenceData.EndDate : '', serverIsOnline, maintanenceStatus]);

    return (
        <MyContext.Provider value={{ urls, alert, SetAlert, serverIsOnline, maintanenceData, maintanenceStatus, progress, LoadingProgress, homeData, servicesData, categoryData, projectData, resumeData, skillData, skillItemData, cardData, resumeItemData, fetchProjectData, fetchProjectDetails, fetchProjectImageDetails }}>
            {props.children}
        </MyContext.Provider>
    )
}

export default MyState
