import React, { useContext, useEffect } from 'react'
import Profile from '../assets/img/my_dp.jpg'
import { Link } from 'react-scroll';
import { NavLink, useLocation } from 'react-router-dom';
import MyContext from '../context/MyContext';

const Header = () => {

  const {urls} = useContext(MyContext)
  const location = useLocation()
  // console.log(location);
  
  // Array of object for Navbar Menu Items
  const NavList = [
    { "id":"1", "to": "Home", "name": "Home"},
    { "id":"2", "to": "About", "name": "About"},
    { "id":"3", "to": "Resume", "name": "Resume"},
    { "id":"4", "to": "Services", "name": "Services"},
    { "id":"5", "to": "Portfolio", "name": "Portfolio"},
    // { "id":"6", "to": "Team", "name": "Team"},
    { "id":"7", "to": "Contact", "name": "Contact"}
  ]
  
  // NavItem Space Manage
  const Nav_li_Man = "nav-item pb-lg-0 pb-3 pe-lg-5"

  useEffect(() => {
    const navbar = document.getElementById('navbarManage');
    location.pathname!=='/'? navbar.classList.add('scrolled'):navbar.classList.remove('scrolled');
    const handleScroll = () => {
      const scrollTop = window.pageYOffset || document.documentElement.scrollTop;
      // Add or remove the 'scrolled' class based on the scroll position
      (scrollTop > 50)? navbar.classList.add('scrolled'): (location.pathname!=='/')?navbar.classList.add('scrolled') :navbar.classList.remove('scrolled');
    };

    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, [location]);

  return (
    <>
      {/* Header Section Start Here */}
      <header className='fixed-top'>
        {/* Navbar Start */}
        <nav id="navbarManage"  className="navbar navbar-dark navbar-expand-lg py-3">
          <div className="container-lg container-fluid px-lg-5">
            <div>
              <button className="navbar-toggler focus-ring focus-ring-dark" type="button" data-bs-toggle="offcanvas"
                data-bs-target="#offcanvasNavbar" aria-controls="offcanvasNavbar">
                <span className="bi bi-list-nested"></span>
              </button>
            </div>

            {/* Name Logo Design Start */}
            <div className='pe-lg-0 pe-5'>
              <NavLink to={urls.HomePage.url} target='_blank' className='text-warning text-decoration-none txt' style={{ cursor: "pointer" }}>
                <div className='border rounded rounded-1 border-warning px-2 py-1'>
                  <h4 className='mb-0'>D.R.</h4>
                </div>
              </NavLink>
            </div>
            {/* Name Logo Design End */}

            <div>
              {/* <a href="#" className='btn btn-outline-warning rounded-1 d-lg-none' style={{ fontFamily: "cambria" }}>Contact Us</a> */}
            </div>

            <div className="offcanvas offcanvas-start" tabIndex="-1" id="offcanvasNavbar" aria-labelledby="offcanvasNavbarLabel"
              style={{ background: "rgba(0,0,0,0.9)" }}>

              {/* Button and Text Show only mobile Devices */}
              <div className="offcanvas-header px-4">
                <img src={Profile} className="img-fluid rounded-circle" alt="my_dp" style={{ width: "18%" }} />
                <div className="fs-3 border-bottom rounded-0 border-warning text-warning txt">Deepak Raikwar</div>
                <hr />
                <button type="button" className="btn btn-outline-warning bi bi-x-lg" data-bs-dismiss="offcanvas"
                  aria-label="Close"></button>
              </div>
              {/* Button and Text End */}

              {/* This body Show in Laptop and tablte devices */}
              <div className="offcanvas-body justify-content-end px-lg-0 px-5 ">

                <ul className="navbar-nav justify-content-start fs-5 align-self-center txt3">
                  {NavList.map((list)=>{
                      return(
                        <li key={list.id} className={Nav_li_Man}>
                            <Link className="my_link" smooth={true} duration={500} spy={true} activeClass="active" data-bs-dismiss="offcanvas" to={list.to}>{list.name}</Link>
                        </li>)
                      })}
                </ul>

                {/* Button for Direct Call Start */}
                <div>
                  <a href={urls.Mobile.url} className='btn btn-outline-warning rounded-0 d-lg-inline d-none btnOutLine' style={{ fontFamily: "cambria" }}><i className='fa fa-phone pe-2'></i>Contact Us</a>
                </div>
                {/* Button for Direct Call End */}

              </div>
            </div>
          </div>

        </nav>
        {/* Navbar End */}
      </header>
      {/* Header Section End Here */}
    </>
  )
}

export default Header