import React, { useContext, useState } from 'react'
import myContext from '../context/MyContext';
import apiService from '../apiservice/apiService';
// import base64 from 'base-64';

const Contact = () => {

  const { urls, SetAlert, LoadingProgress} = useContext(myContext)
  const [inputs, setInputs] = useState({});
  
  let formInput = "form-control rounded-0 focus-ring focus-ring-light"

  const handleChange = (event) => {
    const name = event.target.name;
    const value = event.target.value;
    setInputs(values => ({ ...values, [name]: value }))
  }

  const handleSubmit = async (event) => {
    event.preventDefault();
    try {
      LoadingProgress(20)
      const response = await apiService.post('contact/',inputs)
      LoadingProgress(50)
      if (response.status === 201) {
        setInputs({})
        LoadingProgress(100)
        SetAlert("success", "Message Send Successfully...", 2000, true)
      }
    } catch (error) {
      if (error.response.status === 404) {
        SetAlert("error", "Failed to Send", 3000, true)
      }
      if (error.response.status === 401) {
        SetAlert("error", "Technical Error!", 3000, true)
      }
      console.log(error);
    }
  };

  return (
    <div className='row mt-3' id="contactMng">
      <hr />

      {/* Contact Details Start */}
      <div className="col-lg-4">

        {/* Location Row Start*/}
        <div className="row py-4">
          <div className="col d-flex">
            <div className="contactIcon rounded-1">
              <i className='bi bi-geo-alt'></i>
            </div>
            <div>
              <span className='MyHead3'>Location:</span>
              <br />
              <a href={urls.Location.url} target='_blank' rel='noreferrer'>{urls.Location.title}</a>
            </div>
          </div>
        </div>
        {/* Location Row End */}

        {/* Email Row Start*/}
        <div className="row pb-4">
          <div className="col d-flex">
            <div className="contactIcon rounded-1">
              <i className='bi bi-envelope'></i>
            </div>
            <div>
              <span className='MyHead3'>Email Us At:</span>
              <br />
              <a href={urls.Email.url}>{urls.Email.title}</a>
            </div>
          </div>
        </div>
        {/* Email Row End */}

        {/* Call Row Start*/}
        <div className="row pb-5">
          <div className="col d-flex">
            <div className="contactIcon rounded-1">
              <i className='bi bi-phone'></i>
            </div>
            <div>
              <span className='MyHead3'>Call Us On:</span>
              <br />
              <a href={urls.Mobile.url}>{urls.Mobile.title}</a>
            </div>
          </div>
        </div>
        {/* Call Row End*/}

      </div>
      {/* Contact Details End */}


      <div className="col-lg-1"></div>

      {/* Contact Form Start */}
      <div className="col-lg-7">
        <form id="contactForm" className='txt3' onSubmit={handleSubmit}>
          <h2 className='py-2'>Send a Message</h2>
          <div className="row">
            <div className="col-md-6">
              <input type="text" name='Contact_Name' value={inputs.Contact_Name || ""} onChange={handleChange} className={`${formInput}`} placeholder="Your Name" required />
            </div>
            <div className="col-md-6">
              <input type="email" name='Contact_Email' value={inputs.Contact_Email || ""} onChange={handleChange} className={`${formInput} mt-md-0 mt-3`} placeholder="Your Email" required />
            </div>
          </div>
          <div className="row">
            <div className="col-md-12">
              <input type="text" name='Contact_Subject' value={inputs.Contact_Subject || ""} onChange={handleChange} className={`${formInput}`} placeholder='Subject...' required />
            </div>
          </div>
          <div className="row">
            <div className="col-md-12">
              <textarea name='Contact_Message' value={inputs.Contact_Message || ""} onChange={handleChange} className={`${formInput}`} rows="4" cols="50" placeholder='Message...' required></textarea>
            </div>
          </div>
          <div className="row mt-3">
            <div className="col-md-12">
              <input type="submit" className='btn rounded-0' value="Send Message" />
            </div>
          </div>
        </form>
      </div>
      {/* Contact Form End */}
    </div>
  )
}

export default Contact
