import React from 'react'
import Home from './Home'
import MasterPage from './master/MasterPage'
import About from '../components/About/About'
import Resume from './Resume/Resume'
import Services from './Services/Services'
import Freelancer from './Freelancer'
import Portfilo from './Portfolio/Portfilo'
// import Testimonial from './Testimonial/Testimonial'
// import Team from './Team/Team'
import Contact from './Contact'

const Dashboard = () => {

  return (
    <div className="scrollable-content">
      
      <div id='Home' className="container-fluid px-0 bgHome">
        <Home/>
      </div>
    
      <div id='About' style={{ minHeight: "100vh" }} >
          <MasterPage title="About" subtitle="Learn more about me" containerDesign="p-lg-5 py-5 px-4 shadow border" row={<About/>} />
      </div>

      <div id='Resume' style={{ minHeight: "100vh" }}>
          <MasterPage title="Resume" subtitle="check my resume" containerDesign="p-lg-5 py-5 px-4" row={<Resume/>} />
      </div>

      <div id='Services' style={{ minHeight: "100vh" }} className="container-fluid px-0 bg-light">
          <MasterPage title="services" subtitle="check our services"  containerDesign="p-lg-5 py-5 px-4" row={<Services />} />
      </div>

      <div id="Free" className='bgHire'>
          <Freelancer />
      </div>

      <div id='Portfolio' className='container px-0' style={{ minHeight: "100vh" }}>
          <MasterPage title="Portfolio" subtitle="checkout my projects" containerDesign="p-lg-5 py-5 px-4 shadow-sm border" row={<Portfilo />} />
      </div>

      {/* <div id='bgTestID' className='bgTest'>
          <Testimonial />
      </div>

      <div id='Team' className='container px-0' style={{ minHeight: "100vh" }}>
          <MasterPage title="Team" subtitle="check our team members" containerDesign="p-lg-5 px-4" row={<Team />} />
      </div> */}

      <div id='Contact' style={{ minHeight: "100vh" }}>
          <MasterPage title="get in touch" subtitle="i'm here to help you anytime 24/7 " containerDesign="p-lg-5 py-5 px-4" row={<Contact />} />
      </div>
    </div>
  )
}

export default Dashboard
