import React, { useContext } from 'react'
import myContext from '../../context/MyContext';

const ServerDown = () => {

  const { urls } = useContext(myContext);
  console.log(urls);

  return (
    <div id="ServerDown" className="container-fluid">
      <div className="row bg-1" style={{ height: "100vh" }} >
        <div className="col-md-2 col-0"></div>
        <div id='ServerMsg' className="col-md-8">
          <div className="row py-4 px-md-4 px-1  border bg">
            <div className="col d-flex">
              <i className="bi bi-exclamation-triangle display-5 pe-3"></i>
              <h1 className='txt3 align-self-end m-0'>Error 500</h1>
            </div>
            <div className="row mt-3">
              <hr className='border-light' />
              <div className="col">
                <h3 className='txt m-0 align-self-end'>Web Server is Down . . .</h3>
              </div>
            </div>

            <div id='box1' className="row mt-5">
              <div className="col-2">
                <i class="bi bi-window-fullscreen display-1"></i>
              </div>
              <div className="col-3">
                <i class="bi bi-arrow-left-right display-5"></i>
              </div>
              <div className="col-2">
                <i class="bi bi-cloud display-1"></i>
              </div>
              <div className="col-3">
                <i class="bi bi-arrow-left-right display-5"></i>
              </div>
              <div className="col-2">
                <i class="bi bi-hdd-rack display-1"></i>
              </div>
            </div>

            <div id='box2' className="row">
              <div className="col-2">
                <span className='txt3 fs-5'>Browser</span>
                <br />
                <i class="bi bi-check-circle-fill text-success fs-2"></i>
              </div>
              <div className="col-3"></div>
              <div className="col-2">
                <span className='txt3 fs-5'>REST API</span>
                <br />
                <i class="bi bi-check-circle-fill text-success fs-2"></i>
              </div>
              <div className="col-3"></div>
              <div className="col-2">
                <span className='txt3 fs-5'>Server</span>
                <br />
                <i class="bi bi-x-circle-fill text-danger fs-2"></i>
              </div>
            </div>

            <div id='box3' className="row text-center mt-4 txt3">
              <hr className='border-light' />
              <div className="col-12 py-2">
                <a href={urls.HomePage.url} className='btn btn-outline-warning'><i class="bi bi-arrow-repeat me-2"></i>Try Again</a>
              </div>
            </div>

          </div>
        </div>
        <div className="col-md-2 col-0"></div>
      </div>
    </div>
  )
}

export default ServerDown
