import React, { useContext } from 'react'
import myContext from '../context/MyContext';

const Freelancer = () => {
  const myStyle = {
    background: "rgba(0,0,0,0.5)",
    height: "40vh"
  }
  const {urls} = useContext(myContext);

  return (
    <div className='container-fluid d-inline-flex align-items-center' style={myStyle}>
      <div className="container text-center text-light">
        {/* <h2 className='txt3 my-2'>Call to Action</h2> */}
        <p className=' text-capitalize fs-2 txt3 my-3'> freelancer available for hire. Let's discuss your project!</p>
        <a href={urls.Mobile.url} className='btn btn-outline-warning border-2 rounded-1 fs-6 px-5 py-1 my-3' style={{ fontFamily: "cambria" }}>
          <i className='fa fa-phone pe-2'></i>
          Call us</a>
      </div>
    </div>
  )
}

export default Freelancer
