import React from 'react'

const ServicesItems = (props) => {
    const { icon, title, details } = props;
    
    return (
        <div className="col-lg-4 col-md-6 col-12 px-4 py-3" id='ServiceItems' data-aos="flip-up" data-aos-duration="1000"> 
            <div className="card text-center bg-transparent py-md-4" style={{ width: "100%" }}>
                <div className="card-body">
                    <i className={`${icon} display-4`}></i>
                    <h5 className="card-title h4 mt-3 mb-0 txt3 fw-semibold">{title}</h5>
                    <span id='line'></span>
                    <p className="card-text fw-medium text-capitalize txt5 px-lg-3">{details}</p>
                </div>
            </div>
        </div>
    )
}

export default ServicesItems
