import React, { useContext, useState } from 'react'
import Profile from '../../assets/img/my_dp.jpg'
import { Link } from 'react-router-dom';
import CountUp from '../Countup/CountUp';
import myContext from '../../context/MyContext';
import Skills from '../Skill/Skills';
import MasterPage from '../master/MasterPage';

const About = () => {

  const { urls, homeData } = useContext(myContext)
  const [isExpanded, setIsExpanded] = useState(false);

  let ul_i = "bi bi-chevron-double-right"

  const date = new Date().getDate();
  const month = new Date().getMonth() + 1;
  const year = new Date().getFullYear();
  let dob = (year - 2003)

  if (month >= 10) { if (date >= 7) { dob += 1 } }

  const toggleExpand = () => {
    setIsExpanded(!isExpanded);
  };

  return (
    <>
      <div className='row mt-3'>
        <div id="About" className="col-md-4 mt-2">
          <Link to={urls.HomePage.url} target="_blank">
            {homeData ?
              <img src={homeData.Profile_URL} alt="Profile" className='img-fluid shadow-sm border' />
              :
              <div aria-hidden="true">
                <img src={Profile} className="img-fluid rounded" alt="ProfileImage" />
              </div>
            }
          </Link>

          {/* About Social Section Start Here */}
          <div id='AboutSocial' className='mt-4 d-flex flex-md-wrap flex-nowrap'>
            <span className='h3 txt fw-semibold align-self-center mb-0 pe-3 me-3 social_border'>Follow</span>

            <span className='my-lg-0 my-md-3 my-0'>
              <a href={urls.LinkedIn.url} target='_blank' rel='noreferrer'>
                <i className='fa fa-linkedin shadow-sm'></i>
              </a>
              <a href={urls.GitHub.url} target='_blank' rel='noreferrer'>
                <i className='fa fa-github shadow-sm'></i>
              </a>
              <a href={urls.Twitter.url} target='_blank' rel='noreferrer'>
                <i className='fa fa-twitter shadow-sm'></i>
              </a>
              <a href={urls.Instagram.url} target='_blank' rel='noreferrer'>
                <i className='fa fa-instagram shadow-sm'></i>
              </a>
            </span>
          </div>
          {/* About Social Section End Here */}

        </div>

        {/* <div className="col-md-1"></div> */}

        <div className="col-md-8 mt-md-0 mt-5">

          <h3 className='MyHead2'>Full Stack Developer</h3><hr />

          {/* About Details Section Start Here */}
          <div className='row fw-bold txt3' id="aboutDetails">
            <div className="col-lg-6">
              <ul className='p-0'>
                <li><i className={ul_i}></i>Date of Birth:<span>07 Oct, 2003</span></li>
                <li><i className={ul_i}></i>Website:<a href={urls.HomePage.url} target='_blank' rel='noreferrer'>{urls.HomePage.title}</a></li>
                <li><i className={ul_i}></i>Email:<a href={urls.Email.url}>{urls.Email.title}</a></li>
                <li><i className={ul_i}></i>Phone:<a href={urls.Mobile.url}>{urls.Mobile.title}</a></li>
              </ul>
            </div>
            <div className="col-lg-6">
              <ul className='p-0'>
                <li><i className={ul_i}></i>Age:<span>{dob}</span></li>
                <li><i className={ul_i}></i>LinkedIn:<a href={urls.LinkedIn.url} target='_blank' rel='noreferrer'>raideepak07</a></li>
                <li><i className={ul_i}></i>Degree:<span>Bachelor</span></li>
                <li><i className={ul_i}></i>City:<span>Karwi, Chitrakoot(U.P.)</span></li>
              </ul>
            </div>
          </div>
          {/* About Details Section End Here */}
         {homeData? 
            <p className='txt3 text-muted fs-6 lh-lg' style={{ textAlign: "justify" }}>
            <span>
              {homeData.Details1}&nbsp;
            </span>
            <span id="dots" className={isExpanded ? 'fade-out' : 'fade-in'} style={{ display: isExpanded ? 'none' : 'inline' }}> . . . </span>
            <span id="more" className={isExpanded ? 'fade-in' : 'fade-out'} style={{ display: isExpanded ? 'inline' : 'none' }}>
              { homeData.Details2}
            </span>
          </p>
          :""}

          <button id="myBtn" className='btn btn-sm px-3 text-capitalize fs-6 rounded-0' style={{ fontFamily: "cambria" }} onClick={toggleExpand}>
            <span className='pe-2'>{isExpanded ? 'Read less' : 'Read more'}</span>
            <i className={`bi bi-box-arrow-${isExpanded ? 'up' : 'down'}-right`}></i>
          </button>

        </div>
      </div>

      <div className='pt-5'>
        <CountUp />
      </div>
      <div className=''>
        <MasterPage title="Skills" subtitle="check my technical skills" containerDesign="px-0" row={<Skills />} />
      </div>
    </>
  )
}

export default About