import React, { useContext } from 'react'
import MyContext from '../../context/MyContext'
import bg from '../../assets/img/b5.jpg'
import Typing from '../master/Typing'
import Clock from '../master/Clock'

const NoInternet = () => {

  const {urls} = useContext(MyContext)

    const myStyle = {
        background: `url(${bg})`,
        backgroundSize: "cover",
        backgroundRepeat: "no-repeat"
    }


    // Typing Effect Values
    let sentence = " Please Try |"
    let typingvalues = ['Check Network Cable', 'Restart your Router . . .', 'Reconnect to Wi-Fi . . .', 'Refresh your Page . . .']

    return (

        <div className='container-fluid px-0' style={myStyle} >
            <div className="container px-lg-5 px-md-4 px-3 text-light">
                <div className="row" style={{ height: "100vh" }}>
                    <div className="col-lg-1"></div>
                    <div className="col-lg-10 align-self-center">
                        <div className=''>
                            <span className='fs-3 text-capitalize txt2'><span className='text-danger fs-2'>OOPs! </span>you lost internet connection</span>
                            <div className='mt-3'>
                                <Typing sentence={sentence} color="orange" senSize="fs-4 txt2" valSize="fs-4 txt" values={typingvalues} />
                            </div>
                            <hr />
                            <Clock />
                            <a href={urls.HomePage.url} className='btn btn-sm btn-warning rounded-1 text-capitalize shadow-0 fs-6 mt-5 px-3' style={{ fontFamily: "cambria" }}>
                                <i className='bi bi-arrow-clockwise pe-1'></i>Refresh
                            </a>
                        </div>
                    </div>
                    <div className="col-lg-1"></div>
                </div>
            </div>
        </div>
    )
}

export default NoInternet
