import React, { useContext } from 'react'
import ResumeItem from './ResumeItem'
import myContext from '../../context/MyContext'
import Skeleton from 'react-loading-skeleton'

const ResumeCard = ({ ResumeDetails }) => {

    const { id, Title } = ResumeDetails
    const { resumeItemData } = useContext(myContext)

    return (
        <div className="col-lg-6 mt-5 pe-lg-5">
            <div className="resume_title h2 txt fw-bold">{Title}</div>
            {resumeItemData ?
                resumeItemData.filter(item => item.Title.id === id).map((item, index) => {
                    return (
                        <ResumeItem key={index} course={item.Course} year={item.Year} city={item.City} institute={item.Institute} details={item.Details} />
                    )
                })
                :
                // Placehoder for resumeitem 
                <div className="resume_item px-4">
                    <h6 className='pb-3 fs-2'><Skeleton/></h6>
                    <div className='placeholder-glow'>
                        <span className='placeholder col-4 p-3 me-3'></span>
                        <br className='d-sm-none' />
                        <br className='d-sm-none' />
                        <span className='placeholder col-6 fs-4'></span>
                    </div>
                    <p className='pt-3 fst-italic fw-bold txt5 fs-5'><Skeleton/></p>
                    <p className='fs-5'><Skeleton count={4}/></p>
                </div>
            }
        </div>
    )
}

export default ResumeCard
